@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
.person_container__1SIBm {
	border-radius: 100%;
	height: 3rem;
	width: 3rem;
	margin: 10%;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	color: white;
	cursor: pointer;
	
	transition: all .1s linear;
	position: relative;
}

.person_container__1SIBm:hover {
	height: 3.5rem;
	width: 3.5rem;
}

.person_container__1SIBm:focus {
	height: 3.5rem;
	width: 3.5rem;
}

.person_badge__1lRux {
	height: .5vw;
	width: .5vw;
	border-radius: 100%;
	background-color: rgb(255, 255, 255);

	position: absolute;
	bottom: 10%;
	z-index: 10;
	animation: person_up-down__1_C2V 1s ease-in-out infinite;
	animation-direction: alternate;
}

@keyframes person_up-down__1_C2V {

	from {
		bottom: 5%;
	}
	to {
		bottom: 15%;
	}
}
.letter_title__1-UgS {
	color: white;
	font-size: 2.5rem;
	margin: 2%;
	font-weight: lighter;
	text-decoration: underline;
	-webkit-text-decoration-line: 1px;
	        text-decoration-line: 1px;
}
* {
	margin: 0;
	padding: 0;
}

html {
	font-family: Roboto, Arial, Helvetica, sans-serif;
}

html, body {
	box-sizing: border-box;
	width: 100vw;
}

.blur {
	-webkit-filter: blur(10px);
	        filter: blur(10px);
}

.background {
	background: url(https://images.unsplash.com/photo-1613132272438-9e409d2b2690?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80) no-repeat center center fixed;
	background-size: cover;
	height: 100vh;
	z-index: -100;
	left: 0;
	top: 0;

	overflow: hidden;
}

.container {
	position: fixed;
	left: 0;
	top: 0;
	display: flex;
	flex-direction: row;
	width: 100vw;
	height: 100vh;
	z-index: 1;
}

.row {
	display: flex;
	flex-direction: row;

	justify-content: center;
	align-items: center;
}

.column {
	display: flex;
	flex-direction: column;

	justify-content: flex-start;
	align-items: center;
}

.inbox {
	width: 5%;
	max-width: 70px;
	min-width: 70px;

	-webkit-backdrop-filter: blur(10px);

	        backdrop-filter: blur(10px);
	background-color: rgba(200,200,200,.2);
}

.letters {
	width: 35%;
	min-width: 350px;

	-webkit-backdrop-filter: blur(5px);

	        backdrop-filter: blur(5px);
	background-color: rgba(200,200,200,.1);
}

.writter {
	width: 100%;
	height: 100%;
}

.hide-on-mobile {
	display: inherit;
}

/*=== WIDTH ===*/
